/* src/App.css */
.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header, .footer {
  background-color: #282c34;
  color: white;
  padding: 1rem;
}

.header nav {
  display: flex;
  gap: 1rem;
}

.header a {
  color: white;
  text-decoration: none;
}

.footer {
  margin-top: auto;
  text-align: center;
}

.login-container {
  max-width: 400px;
  margin: 2rem auto;
  padding: 2rem;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.login-container form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.search-bar {
  margin: 2rem 0;
  display: flex;
  gap: 0.5rem;
}

.search-bar input {
  flex-grow: 1;
  padding: 0.5rem;
}

.videos-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem;
}

.video-card {
  border: 1px solid #ddd;
  padding: 1rem;
  border-radius: 8px;
}